export default {
    /**
     * loadScript - 加载script脚本
     * @param script
     * @return Promise
     */
    loadScript: function (script) {
        return new Promise(function (resolve) {
            var el = document.createElement('script');
            el.addEventListener('load', function () {
                resolve();
            });
            el.src = script;
            document.getElementsByTagName('head')[0].appendChild(el);
        });
    },
    /**
     * loadLink - 加载css文件
     * @param link
     * @return Promise
     */
    loadLink: function (link) {
        return new Promise(function (resolve) {
            var el = document.createElement('link');
            el.setAttribute('rel', 'stylesheet');
            el.addEventListener('load', function () {
                resolve();
            });
            el.href = link;
            document.getElementsByTagName('head')[0].appendChild(el);
        });
    },
    // @ts-ignore
    /**
     * loadRemoteResource - 加载远程所有的脚本和样式资源
     * @param scripts
     * @param links
     * @return Promise
     */
    loadRemoteResource: function (_a) {
        var _this = this;
        var scripts = _a.scripts, links = _a.links;
        // @ts-ignore
        return Promise.all([].concat(
        // @ts-ignore
        (scripts || []).map(function (script) { return _this.loadScript(script); }), (links || []).map(function (link) { return _this.loadLink(link); })));
    },
};
